
import { VueDraggableNext } from "vue-draggable-next";
import {
  defineComponent,
  reactive,
  ref,
  computed,
  toRefs,
  shallowRef,
} from "vue";
import Loader from "@/components/load-image/Loader.vue";
import Item from "@/components/Item.vue";
import Slider from "@/components/slider/Slider.vue";
import SliderItem from "@/components/slider/SliderItem.vue";
import ToolBox from "@/components/tool-box/ToolBox.vue";
import Tool from "@/components/tool-box/Tool.vue";
import {
  ElMessageBox,
  ElInput,
  ElDialog,
  ElSelect,
  ElOption,
  ElIcon,
} from "element-plus";
import { Plus, Lock, Edit, Loading, ArrowUp } from "@element-plus/icons";
import { cloneDeep, findIndex } from "lodash";
import axios from "axios";
export default defineComponent({
  name: "Home",
  components: {
    Loader,
    Item,
    Slider,
    SliderItem,
    ToolBox,
    Tool,
    ElInput,
    ElDialog,
    ElSelect,
    ElOption,
    ElIcon,
    Edit,
    Loading,
    Plus,
    ArrowUp,
    draggable: VueDraggableNext,
  },
  setup() {
    /*  */

    // 
    let list = reactive([] as any[]);
    // 
    const idx = list.length > 0 ? list[list.length - 1].id : 0;
    let form = reactive({
      id: idx,
      title: "",
      img: "",
      complete: false,
      date: "",
      detail: "",
    });

    /*  */

    const tools = reactive({
      // 
      isOverlay: false as boolean,
      // ()
      dialog: true as boolean,
      //  (add | edit | complete | view)
      mode: "" as string,
      // drawer ()
      drawer: false as boolean,
      // 
      drawerTitle: "" as string,
      // 
      disableNextAdd: true as boolean,
      // 
      lock: false as boolean,
      // scroll
      isScroll: 0 as number,
    });

    // switch icon
    const active = shallowRef(Lock as any);
    const inactive = shallowRef(Plus as any);

    //  callback
    const loader = ref(null as any);
    const loaderReset = () => {
      loader.value?.reset();
    };

    // slider 
    const slider = ref(null as any);
    const sliderReset = () => {
      slider.value?.reset();
    };

    /*  -  */

    const completeForm = reactive({
      // 
      disabledDate(time: any) {
        return time.getTime() > Date.now();
      },
      // 
      shortcuts: [
        {
          text: "Today",
          value: new Date(),
        },
        {
          text: "Yesterday",
          value: () => {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            return date;
          },
        },
        {
          text: "A week ago",
          value: () => {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            return date;
          },
        },
      ],
    });

    /*  */
    const startSetting = reactive({
      genNum: 5 as number,
      genOptions: [5, 10, 20, 50, 100] as number[],
    });

    /* Computed */
    // input
    const inputStyle = computed(() => {
      return {
        textAlign: "center",
        fontSize: "32px",
        padding: "20px",
        marginTop: "20px",
        width: "80%",
      };
    });

    // 
    const compRate = computed(() => {
      let count = 0;
      list.forEach((x) => {
        if (x.complete) count++;
      });
      return count;
    });

    // 
    const showAddItem = computed(() => {
      return tools.lock || list.length > 0
        ? {
            width: "0px",
          }
        : {
            width: "300px",
          };
    });

    /**
     * 
     *
     * @param {number} child - 
     * @param {number} parent - 
     */
    const setPercentage = function (child: number, parent: number) {
      if (!parent) return "0%";
      return `${Math.round((child / parent) * 100)}%`;
    };

    return {
      list,
      form,
      loader,
      loaderReset,
      slider,
      sliderReset,
      inputStyle,
      compRate,
      showAddItem,
      active,
      inactive,
      ...toRefs(tools),
      ...toRefs(startSetting),
      ...toRefs(completeForm),
      setPercentage,
    };
  },
  mounted() {
    // list
    this.lock = Boolean(this.list.length > 0);
    // 
    this.$nextTick(() => {
      window.document.addEventListener("scroll", this.onScroll);
    });
  },
  methods: {
    /**
     * 
     *
     * @param {number} num - 
     */
    autoStart(num: number) {
      let scheduled = [];
      for (let i = 0; i < num; i++) {
        scheduled.push(this.generate());
      }
      this.isOverlay = true;
      this.dialog = false;
      Promise.all(scheduled).then((res) => {
        const timer = setTimeout(() => {
          this.isOverlay = false;
          this.genNum = 0;
          clearTimeout(timer);
        }, 500);
      });
    },
    /**
     * 
     */
    selfStart() {
      this.dialog = false;
      this.itemAdd();
    },
    /**
     * 
     *
     * @param {object} item - 
     */
    handleItem(item: any) {
      // 
      const { id, title, img, date, detail, complete } = item;
      this.form.id = id;
      this.form.title = title;
      this.form.img = img;
      this.form.date = date;
      this.form.detail = detail;
      this.form.complete = complete;

      // 
      if (this.lock) {
        if (item.complete) {
          // 
          this.itemView();
        } else {
          // 
          this.itemComplete();
        }
      } else {
        // 
        this.itemEdit();
      }
    },
    /**
     * 
     */
    itemView() {
      this.setMode("view", `View Item`);
    },
    /**
     * 
     */
    itemComplete() {
      this.setMode("complete", `Complete Item`);
    },
    /**
     * 
     */
    itemEdit() {
      this.setMode("edit", "Edit Item Setting");
      this.disableNextAdd = false;
    },
    /**
     * 
     */
    itemAdd() {
      this.setMode("add", "Add Item");
    },
    /**
     * 
     *
     * @param {number} id - id
     */
    handleDelete(id: number) {
      const idx = findIndex(this.list, (x) => x.id === id);
      if (idx === -1) return;
      this.list.splice(idx, 1);
    },
    /**
     * 
     */
    handleSubmit() {
      this.drawer = false;
      const item = cloneDeep(this.form);
      const idx = findIndex(this.list, (x) => x.id === item.id);

      if (idx === -1) {
        if (this.mode === "add" || this.mode === "edit") this.list.push(item);
      } else {
        this.list[idx] = item;
        if (this.mode === "complete" || this.mode === "view") {
          this.list[idx].complete = true;
        }
      }
      // 
      this.resetForm();
    },
    /**
     *  drawer callback
     *
     * @param {function} done - 
     */
    handleClose(done: any) {
      if (this.form.complete) {
        done();
      } else {
        ElMessageBox.confirm("Are you sure you want to close this?")
          .then(() => {
            done();
            this.loaderReset();
            this.sliderReset();
          })
          .catch(() => {
            // catch error
          });
      }
    },
    /**
     * 
     *
     * @param {number | string} num -  | start | end
     */
    handleSlide(num: number | string) {
      this.disableNextAdd = Boolean(num !== "end");
      if (num === 0 || num === "start") {
        if (this.mode === "add" || this.mode === "edit")
          this.disableNextAdd = Boolean(this.form.title.length === 0);
        if (this.mode === "complete")
          this.disableNextAdd = Boolean(this.form.date.length === 0);
      }
      if (num === 1) {
        this.disableNextAdd = Boolean(this.form.img.length === 0);
      }
    },
    /**
     *  Input 
     *
     * @param {string | number} val - input 
     */
    handleInput(val: string | number) {
      this.disableNextAdd = Boolean(val.toString().length === 0);
    },
    /**
     * 
     *
     * @param {array} data - 
     */
    handleSelect(data: string[]) {
      this.form.img = data[0] ? data[0] : "";
      this.disableNextAdd = Boolean(data.length === 0);
    },
    /**
     * 
     */
    resetForm() {
      this.$nextTick(() => {
        const id = this.list[this.list.length - 1]
          ? this.list[this.list.length - 1].id
          : 0;
        this.form.id = id + 1;
        this.form.title = "";
        this.form.img = "";
        this.form.date = "";
        this.form.detail = "";
        this.form.complete = false;
      });
      this.loaderReset();
    },
    /**
     * 
     */
    async generate() {
      // 
      await axios
        .get("https://www.boredapi.com/api/activity")
        .then((res) => {
          // id
          let picId = Math.floor(Math.random() * 300);
          // 
          axios
            .get(`https://picsum.photos/id/${picId}/300/180`)
            .catch((err) => {
              // id0
              picId = 0;
            })
            .finally(() => {
              // 
              this.resetForm();
              // 
              this.list.push({
                id: this.form.id,
                title: res.data.activity as string,
                img: `https://picsum.photos/id/${picId}/300/180`,
                complete: false,
                date: "",
                detail: "",
              });
              // drawer
              this.drawer = false;
            });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    /**
     * mode
     *
     * @param {string} mode -  (add | edit | complete | view)
     * @param {string} title - drawer 
     */
    setMode(mode: string, title: string) {
      // 
      this.mode = mode;
      //  drawer 
      this.drawerTitle = title;
      this.drawer = true;
    },
    /**
     *  scroll  isScroll 
     */
    onScroll() {
      this.isScroll = Math.floor(
        window.pageYOffset !== undefined
          ? window.pageYOffset
          : (
              document.documentElement ||
              document.body.parentNode ||
              document.body
            ).scrollTop
      );
    },
    backToTop() {
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
  },
});
